	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"No More Spooks, Honest!","date":"31 Jan 2002","slug":"/2002-01-31-no-more-spooks-honest/","tags":"Y2002"},"html":"<p>No English links (maybe tomorrow in FAZ?), but the main story today is a Bundestag hearing on the NPD Informant Affair. Schilly swears that there are no more governemnt informants cited in the complaint, at least he's pretty sure. Noone is calling for Schilly to resign anymore, and all parties (even the FDP, which was wavering) agree to resubmit the complaint, but the court decision will most likely not come until after the fall election.<br />\n<b>Update</b> I knew the FAZ would come through: <a href=\"http://www.faz.com/IN/INtemplates/eFAZ/docmain.asp?rub={B1311FCC-FBFB-11D2-B228-00105A9CAF88}&amp;doc={A99B6C17-8241-444A-B83C-20267968B947}\">Interior Minister Refuses Blame</a></p>"},"next":{"fields":{"title":"Lost On Penalties","date":"31 Jan 2002","slug":"/2002-01-31-lost-on-penalties/"}},"previous":{"fields":{"title":"Raining Red Roses","date":"01 Feb 2002","slug":"/2002-02-01-raining-red-roses/"}}},{"node":{"fields":{"title":"Lost On Penalties","date":"31 Jan 2002","slug":"/2002-01-31-lost-on-penalties/","tags":"Y2002"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2002/01/crhccsalsa.jpg\" height=\"244\" width=\"325\" border=\"0\" alt=\"crhccsalsa.jpg: \" /></p>\n<p>Here's the <a href=\"/2002/01/30\">cottage cheese and salsa</a> picture I promised last night.</p>\n<p><a href=\"http://diveintomark.org\">diveintomark</a> has a new URL. Please make a note of it.</p>"},"next":{"fields":{"title":"Stem Cells","date":"30 Jan 2002","slug":"/2002-01-30-stem-cells/"}},"previous":{"fields":{"title":"No More Spooks, Honest!","date":"31 Jan 2002","slug":"/2002-01-31-no-more-spooks-honest/"}}},{"node":{"fields":{"title":"Stem Cells","date":"30 Jan 2002","slug":"/2002-01-30-stem-cells/","tags":"Y2002"},"html":"<p>FAZ: <a href=\"http://www.faz.com/IN/INtemplates/eFAZ/docmain.asp?rub={B1311FCC-FBFB-11D2-B228-00105A9CAF88}&amp;doc={46B7DD8B-428B-4BD1-B3BE-F32E715A6395}\">Vote on Import of Stem Cells Wide Open</a> On certain issues, the Bundestag parties decide to drop party discipline and let the members vote on conscience. Previous such debates (abortion, moving the capital) have shown German democracy at its best.<br />\nBeing able to form consensus and make decisions on ethical issues (i.e. abortion, gun control) is something I find painfully lacking in US politics.<br />\n<b>Update</b> The compromise solution, allowing only restricted import of embryo stem cells, was approved, rather than a total ban or the unrestricted import.</p>"},"next":{"fields":{"title":"Donkey On The Edge","date":"30 Jan 2002","slug":"/2002-01-30-donkey-on-the-edge/"}},"previous":{"fields":{"title":"Lost On Penalties","date":"31 Jan 2002","slug":"/2002-01-31-lost-on-penalties/"}}}],"pathPrefix":"","first":false,"last":false,"index":934,"pageCount":1101}}

